@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
body {
  color: #6F6F6E;
  font-family: 'Open Sans', sans-serif; }

.user-profile-image {
  max-width: 200px; }

.row-bg {
  background-image: url("../img/internal.jpg");
  background-size: cover; }
  .row-bg h1 {
    margin: 50px auto;
    font-size: 5em;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif; }

.container-bg {
  background-image: url("../img/home.jpg");
  background-size: cover;
  background-position: center center;
  margin-bottom: 50px; }
  .container-bg h1 {
    margin: 50px auto;
    font-size: 10em;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
    background-color: rgba(0, 0, 0, 0.5); }

/* Large Devices, Wide Screens */
/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {
  .container-bg h1 {
    font-size: 8em; } }

/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .container-bg h1 {
    font-size: 5em; } }

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) {
  .container-bg h1 {
    font-size: 3em; } }

/* Custom, iPhone Retina */
.btn-red {
  background-color: #E42029;
  color: #fff; }
  .btn-red:hover {
    background-color: #fff;
    color: #E42029; }

.btn-circle {
  border-radius: 20px;
  font-size: 20px;
  font-weight: bold;
  width: 20px;
  height: 20px;
  padding: 0 0 0 2px;
  line-height: 16px; }

.navbar-lobby {
  background-color: #0F74BA;
  border-radius: 0px;
  margin-bottom: 0px; }
  .navbar-lobby li a {
    color: #fff; }
    .navbar-lobby li a:hover {
      background-color: transparent; }

h2 {
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
  font-weight: bold;
  margin: 30px 0; }

p {
  font-family: 'Open Sans', sans-serif; }

.table-title {
  text-transform: uppercase;
  background-color: #6F6F6E;
  color: #fff;
  padding: 10px 0;
  margin-bottom: 0; }

table th {
  text-transform: uppercase;
  color: #E42029; }

.modal-header {
  background-color: #6F6F6E;
  color: #fff; }

.brand-box {
  background-color: #fff;
  padding: 40px;
  margin-bottom: 50px; }

.card {
  padding-top: 20px;
  margin: 10px 0 20px 0;
  background-color: rgba(214, 224, 226, 0.2);
  border-top-width: 0;
  border-bottom-width: 2px;
  box-shadow: none;
  box-sizing: border-box; }

.card .card-heading {
  padding: 0 20px;
  margin: 0; }

.card .card-heading.simple {
  font-size: 20px;
  font-weight: 300;
  color: #777;
  border-bottom: 1px solid #e5e5e5; }

.card .card-heading.image img {
  display: inline-block;
  width: 46px;
  height: 46px;
  margin-right: 15px;
  vertical-align: top;
  border: 0;
  border-radius: 50%; }

.card .card-heading.image .card-heading-header {
  display: inline-block;
  vertical-align: top; }

.card .card-heading.image .card-heading-header h3 {
  margin: 0;
  font-size: 14px;
  line-height: 16px;
  color: #262626; }

.card .card-heading.image .card-heading-header span {
  font-size: 12px;
  color: #999999; }

.card .card-body {
  padding: 0 20px;
  margin-top: 20px; }

.card .card-media {
  padding: 0 20px;
  margin: 0 -14px; }

.card .card-media img {
  max-width: 100%;
  max-height: 100%; }

.card .card-actions {
  min-height: 30px;
  padding: 0 20px 20px 20px;
  margin: 20px 0 0 0; }

.card .card-comments {
  padding: 20px;
  margin: 0;
  background-color: #f8f8f8; }

.card .card-comments .comments-collapse-toggle {
  padding: 0;
  margin: 0 20px 12px 20px; }

.card .card-comments .comments-collapse-toggle a,
.card .card-comments .comments-collapse-toggle span {
  padding-right: 5px;
  overflow: hidden;
  font-size: 12px;
  color: #999;
  text-overflow: ellipsis;
  white-space: nowrap; }

.card-comments .media-heading {
  font-size: 13px;
  font-weight: bold; }

.card.people {
  position: relative;
  display: inline-block;
  width: 170px;
  height: 300px;
  padding-top: 0;
  margin-left: 20px;
  overflow: hidden;
  vertical-align: top; }

.card.people:first-child {
  margin-left: 0; }

.card.people .card-top {
  position: absolute;
  top: 0;
  left: 0;
  display: inline-block;
  width: 170px;
  height: 150px;
  background-color: #ffffff; }

.card.people .card-top.green {
  background-color: #53a93f; }

.card.people .card-top.blue {
  background-color: #427fed; }

.card.people .card-info {
  position: absolute;
  top: 150px;
  display: inline-block;
  width: 100%;
  height: 101px;
  overflow: hidden;
  background: #ffffff;
  box-sizing: border-box; }

.card.people .card-info .title {
  display: block;
  margin: 8px 14px 0 14px;
  overflow: hidden;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  color: #404040; }

.card.people .card-info .desc {
  display: block;
  margin: 8px 14px 0 14px;
  overflow: hidden;
  font-size: 12px;
  line-height: 16px;
  color: #737373;
  text-overflow: ellipsis; }

.card.people .card-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  display: inline-block;
  width: 100%;
  padding: 10px 20px;
  line-height: 29px;
  text-align: center;
  box-sizing: border-box; }

.card.hovercard {
  position: relative;
  padding-top: 0;
  overflow: hidden;
  text-align: left;
  background-color: rgba(214, 224, 226, 0.2); }

.card.hovercard .cardheader {
  background-size: cover;
  height: 135px; }
  .card.hovercard .cardheader a.hovertitle {
    display: none;
    width: 100%;
    height: 100%;
    background-color: rgba(228, 32, 41, 0.5); }
    .card.hovercard .cardheader a.hovertitle h3 {
      text-align: center;
      margin: 0;
      padding-top: 15%;
      color: #fff;
      text-decoration: none; }
    .card.hovercard .cardheader a.hovertitle:hover {
      color: #fff;
      text-decoration: none; }
  .card.hovercard .cardheader:hover .hovertitle {
    display: block; }
  .card.hovercard .cardheader a.hovertitle-soon {
    background-color: rgba(100, 100, 100, 0.5);
    display: block; }

.cardheader.trato {
  background: url("../img/igualdad.jpg"); }

.cardheader.roles {
  background: url("../img/mascara.jpg"); }

.cardheader.viajes {
  background: url("../img/viaje.jpg"); }

.card.hovercard .avatar {
  position: relative;
  top: -35px;
  margin-bottom: -35px;
  right: 15px;
  float: right; }

.card.hovercard .avatar img {
  width: 70px;
  height: 70px;
  max-width: 70px;
  max-height: 70px;
  border-radius: 50%;
  border: 5px solid rgba(255, 255, 255, 0.5); }

.card.hovercard .info {
  padding: 15px 15px 10px; }

.card.hovercard .info .title {
  margin-bottom: 4px;
  font-size: 18px;
  line-height: 1;
  color: #262626;
  vertical-align: middle; }

.card.hovercard .info .desc {
  overflow: hidden;
  font-size: 12px;
  line-height: 20px;
  color: #737373;
  text-overflow: ellipsis; }

.card.hovercard .bottom {
  padding: 0 20px;
  margin-bottom: 17px; }

.footer-lobby {
  background-image: url("../img/footer.jpg");
  background-size: cover;
  background-position: bottom;
  margin-top: 100px;
  background-color: #0F74BA; }
  .footer-lobby p.lead {
    margin-top: 30px;
    color: #fff;
    font-weight: normal; }
  .footer-lobby img {
    margin: 0 auto; }

